body {
    background: #252525;
}
* {
    color: green;
}

//
.article-node {
    margin: 0;
    position: absolute;
    &:hover {    
        cursor: pointer;
    }
}

.article-thumbnail {
    position: absolute;
    border-radius: 50%; // Make it a circle
}

.article-infos {
    background-color: white;
    border-radius: 2px;
    width: 200px;
    transition: all 1.3s;
    // height is controlled in ArticleNode.ts
    //
    margin: 0;
    position: absolute;
    display: none;
    &:hover {
        visibility: visible;
    }
}
// Show infos on node hover
.article-thumbnail:hover + .article-infos,
.article-infos:hover {
    display: block;
    transform: scale(2);
}
